function connectWebViewJavascriptBridge(callback) {
  if (window.WebViewJavascriptBridge) {
    callback(window.WebViewJavascriptBridge);
  } else {
    document.addEventListener(
      'WebViewJavascriptBridgeReady',
      function () {
        callback(window.WebViewJavascriptBridge);
      },
      false
    )
  }
}

function callHandler(code, handler_arg, args) {
  if (browser.versions.mobile) {//判断是否是移动设备打开。browser代码在下面
    if (!window.WebViewJavascriptBridge) {
      console.error("ghaier: not found WebViewJavascriptBridge from window");
    } else {
      window.WebViewJavascriptBridge.callHandler(code, handler_arg, function (responseData) {
        if (responseData) {
          console.log(responseData);
          if (args.success) {
            typeof args.success == "function" & args.success(responseData);
          }
        } else {
          // fail
          if (args.fail) {
            typeof args.fail == "function" & args.fail(responseData);
          }
        }
        if (args.complete) {
          typeof args.complete == "function" & args.complete(responseData);
        }
      });
    }
  } else {
    //返回mock数据

    initBridge();

    if (window.WebViewJavascriptBridge) {
      window.WebViewJavascriptBridge.callHandler(code, handler_arg, function (responseData) {
        if (responseData) {
          console.log(responseData);
          if (args.success) {
            typeof args.success == "function" & args.success(responseData);
          }
        } else {
          // fail
          if (args.fail) {
            typeof args.fail == "function" & args.fail(responseData);
          }
        }
        if (args.complete) {
          typeof args.complete == "function" & args.complete(responseData);
        }
      });

    }

  }


}

if (window && !window.jsbridge_inited) {
  connectWebViewJavascriptBridge(function (bridge) {
    bridge.init(function (message, responseCallback) {
      //bridge初始化
    });
    bridge.registerHandler("JSKey", function (data, responseCallback) {
      //native通过"JSKey"关键字找到对应的handler
      //data是native传过来的数据
      //responseCallback 是native与js通信的回调，若responseCallback不为空，直接responseCallback(responseData)即可
      responseCallback("js responseCallback")
    });
  });
  window.jsbridge_inited = true;

}


var initBridge = function () {
  var messagingIframe
  var sendMessageQueue = []
  var receiveMessageQueue = []
  var messageHandlers = {}

  var CUSTOM_PROTOCOL_SCHEME = 'wvjbscheme'
  var QUEUE_HAS_MESSAGE = '__WVJB_QUEUE_MESSAGE__'

  var responseCallbacks = {}
  var uniqueId = 1

  function _createQueueReadyIframe(doc) {
    messagingIframe = doc.createElement('iframe')
    messagingIframe.style.display = 'none'
    messagingIframe.src = CUSTOM_PROTOCOL_SCHEME + '://' + QUEUE_HAS_MESSAGE
    doc.documentElement.appendChild(messagingIframe)
  }

  function init(messageHandler) {
    var receivedMessages = receiveMessageQueue
    receiveMessageQueue = null
    for (var i = 0; i < receivedMessages.length; i++) {
      _dispatchMessageFromObjC(receivedMessages[i])
    }
  }

  function send(data, responseCallback) {
    _doSend({ data: data }, responseCallback)
  }

  function registerHandler(handlerName, handler) {
    messageHandlers[handlerName] = handler
  }

  function callHandler(handlerName, data, responseCallback) {
    _doSend({ handlerName: handlerName, data: data }, responseCallback)
  }

  function _doSend(message, responseCallback) {
    if (responseCallback) {
      var callbackId = 'cb_' + (uniqueId++) + '_' + new Date().getTime()
      responseCallbacks[callbackId] = responseCallback
      message['callbackId'] = callbackId
    }
    sendMessageQueue.push(message)
    messagingIframe.src = CUSTOM_PROTOCOL_SCHEME + '://' + QUEUE_HAS_MESSAGE
  }

  function _fetchQueue() {
    var messageQueueString = JSON.stringify(sendMessageQueue)
    sendMessageQueue = []
    return messageQueueString
  }

  function _dispatchMessageFromObjC(messageJSON) {

  }

  function _handleMessageFromObjC(messageJSON) {
    if (receiveMessageQueue) {
      receiveMessageQueue.push(messageJSON)
    } else {
      _dispatchMessageFromObjC(messageJSON)
    }
  }

  window.WebViewJavascriptBridge = {
    init: init,
    send: send,
    registerHandler: registerHandler,
    callHandler: callHandler,
    _fetchQueue: _fetchQueue,
    _handleMessageFromObjC: _handleMessageFromObjC
  }

  var doc = document
  _createQueueReadyIframe(doc)
  var readyEvent = doc.createEvent('Events')
  readyEvent.initEvent('WebViewJavascriptBridgeReady')
  readyEvent.bridge = WebViewJavascriptBridge
  doc.dispatchEvent(readyEvent)
}




